import React from "react";
import Typewriter from "typewriter-effect";

function Type() {
  return (
    <Typewriter
      options={{
        strings: [
          "Etudiant en Data Mining",
          "Dveloppeur Python",
          "Dveloppeur R",
          "Dveloppeur SQL",
          "Passionn d'OSINT",
        ],
        autoStart: true,
        loop: true,
        deleteSpeed: 50,
      }}
    />
  );
}

export default Type;