import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import InterCard from "./InternCard";
import Particle from "../Particle";
import StagePremierAnnee from "../../Assets/Projects/qalamsoftware.png";
import StageDeuxiemeAnnee from "../../Assets/Projects/qalamsoftware.png";
// import Tableau_de_synthese from "./tableauSynthese";





function Intern() {
  return (
    <Container fluid className="project-section">
      <Particle />
      <Container>
        <h1 className="project-heading">
          Mes  <strong className="purple">Expricences</strong>
        </h1>
        <Row style={{ justifyContent: "center", paddingBottom: "10px" }}>
          <Col md={4} className="project-card">
            <InterCard
              imgPath={StagePremierAnnee}
              isBlog={false}
              title="Stage premire anne BTS SIO"
              description="Stage de validation de la premire anne de BTS SIO, effectu entirement  distance : L'objectif principal tait de dvelopper la prsence en ligne de l'organisation en prsentant l'entreprise, les services proposs et ses partenaires,  travers une refonte complte de leurs site  l'epoque."
              BTdownload="https://drive.google.com/file/d/1HtQrp-AcvV1HsK5neT0k8iPwp0QX59kK/view?usp=sharing"
            />
          </Col>


          <Col md={4} className="project-card">
            <InterCard
              imgPath={StageDeuxiemeAnnee}
              isBlog={false}
              title="Stage deuxime anne BTS SIO"
              description="Stage de validation de la deuxime anne de BTS SIO, effectu intgralement en distanciel : Le projet avait pour objectif d'amliorer le site conu lors du prcdent stage, visant  accrotre son attractivit et  introduire de nouvelles fonctionnalits."
              BTdownload="https://drive.google.com/file/d/1XiV9_sxdZCmdDfbGv0iBoD2vDKmE-ugb/view?usp=sharing"
            />
            
          </Col>
         
        </Row>

        {/* <Tableau_de_synthese /> */}

      </Container>
    </Container>
  );
}

export default Intern;
